<template>
    <div id="app">
        <div class="container">
            <div class="wrapper">
                <v-btn
                    bg="#f5f5f5"
                    block
                    rounded="4"
                    :disable="loading"
                    @click="
                        () => {
                            $store.dispatch('fetchCurrency'), (show = true)
                        }
                    "
                >
                    <span v-if="!loading">Get data</span>
                    <v-loader v-else size="20" />
                </v-btn>
                <div v-show="show">
                    <Table v-if="!loading && !error" :items="currency" />
                    <span class="text" v-else-if="error">no data</span>
                    <v-loader v-else-if="loading" />
                </div>
            </div>
        </div>
        <v-alert v-if="success" :type="success">{{ success }}</v-alert>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Table from './components/Table.vue'
export default {
    name: 'App',
    components: { Table },
    computed: mapGetters(['loading', 'error', 'success', 'currency']),
    data() {
        return {
            show: false
        }
    }
}
</script>

<style lang="scss" scoped>
#app {
    min-height: 100vh;
    color: #2c3e50;
    display: flex;
    align-items: center;
    justify-content: center;

    .container {
        max-width: 462px;
        width: 100%;
        margin: auto;
        padding: 20px;
    }

    .wrapper {
        border: 1px solid rgba(#000, 0.3);
        border-radius: 2px;
        padding: 34px 10px;
        width: 100%;

        .btn {
            margin: auto;
            max-width: 140px;
            font-weight: bold;
            margin-bottom: 30px;

            .loader {
                height: 20px;
                width: 20px;
            }
        }
    }

    .text {
        text-align: center;
        display: block;
    }
}
</style>
