import getData from '../plugins/getDataFunc'
import { payload } from '../mocData'

export default {
    state: {
        currency: []
    },
    mutations: {
        setCurrency: (state, payload) => (state.currency = payload)
    },
    actions: {
        fetchCurrency: async ({ commit }) => {
            commit('clearError')
            commit('setLoading', true)
            try {
                const { stocks, start, current } = await getData(payload)
                const data = []
                for (let i = 0; i < stocks.length; i++) {
                    data.push({
                        stocks: stocks[i],
                        start: start[i],
                        current: current[i]
                    })
                }

                data.sort(function(a, b) {
                    const nameA = a.stocks.toLowerCase(),
                        nameB = b.stocks.toLowerCase()
                    if (nameA < nameB) return -1
                    if (nameA > nameB) return 1
                    return 0
                })

                commit('setCurrency', data)
                commit('setSuccess', 'Success')
                setTimeout(() => commit('clearSuccess'), 600)
                commit('setLoading', false)
            } catch (error) {
                commit('setCurrency', {})
                commit('setLoading', false)
                commit('setError', true)
            }
        }
    },

    getters: {
        currency: state => state.currency
    }
}
