<template>
    <span
        v-if="tag === 'span'"
        class="btn"
        :class="[rounded && `rounded-${rounded}`, block && 'block', disable && 'disable']"
        :style="bg && `background-color: ${bg}`"
        @click="handleClick"
    >
        <slot></slot>
    </span>
    <a
        v-else-if="tag === 'a'"
        class="btn"
        :class="[rounded && `rounded-${rounded}`, block && 'block', disable && 'disable']"
        :style="bg && `background-color: ${bg}`"
        :href="href"
        @click="handleClick"
    >
        <slot></slot>
    </a>

    <button
        v-else
        class="btn"
        :class="[rounded && `rounded-${rounded}`, block && 'block', disable && 'disable']"
        :style="bg && `background-color: ${bg}`"
        :disabled="disable"
        @click="handleClick"
    >
        <slot></slot>
    </button>
</template>

<script>
export default {
    props: {
        tag: {
            type: String,
            default: 'button'
        },
        bg: {
            type: String,
            default: '#b5b5b5'
        },

        color: {
            type: String,
            default: '#fff'
        },

        disable: {
            type: Boolean,
            default: false
        },

        href: {
            type: String,
            default: '#'
        },

        rounded: {
            type: String,
            default: '0'
        },

        block: {
            type: Boolean,
            default: false
        }
    },

    methods: {
        handleClick() {
            this.$emit('click')
        }
    }
}
</script>

<style lang="scss" scoped>
.btn {
    font-size: 16px;
    color: #4c4c4c;
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
    border: 1px solid #eeeeee;

    &:active {
        transform: scale(0.9);
    }

    .loader {
        height: 60%;
    }

    &.block {
        display: block;
        width: 100%;
    }

    &.rounded {
        @for $i from 4 through 18 {
            &-#{$i} {
                border-radius: #{$i * 1px};
            }
        }
    }

    &.disable {
        background-color: #b5b5b5;
        cursor: no-drop;

        &:active {
            transform: scale(1);
        }
    }
}
</style>
