<template>
    <div class="table">
        <div class="table-head">
            <span class="table-item">Stocks</span>
            <span class="table-item">Start</span>
            <span class="table-item">Change</span>
        </div>

        <div class="table-body">
            <div class="tr" v-for="({ stocks, start, current }, i) of items" :key="i">
                <span class="table-item">{{ stocks }}</span>
                <span class="table-item">{{ start }}</span>
                <span
                    class="table-item"
                    :class="(+current - +start).toFixed(2) < 0 ? 'danger' : 'success'"
                >
                    {{
                        (current - start).toFixed(2) >= 0
                            ? `+${(current - start).toFixed(2)}`
                            : (current - start).toFixed(2)
                    }}
                </span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        items: {
            type: Array,
            require: true,
            default: () => []
        }
    }
}
</script>

<style lang="scss" scoped>
.table {
    border: 1px solid #eeeeee;
    border-radius: 4px;

    &-head,
    &-body .tr {
        display: flex;
        justify-content: space-between;
        align-items: stretch;
        color: #242424;
    }

    &-head {
        background-color: #f5f5f5;
        .table-item {
            font-weight: bold;
            border-top: none;
            text-transform: capitalize;
        }
    }

    &-item {
        width: calc(100% / 3 - 10px);
        text-align: center;
        padding: 10px;
        border-top: 1px solid #eeeeee;

        &.danger {
            color: red;
        }

        &.success {
            color: green;
        }
    }
}
</style>
